.header {
  height: 45px;
  line-height: 45px;
  background-color: #f0f0f0;
  padding: 0 10px;
  text-align: center;
  color: #f63;
  position: relative;
  z-index: 300;
}

.header__back {
  padding-left: 16px;
  position: absolute;
  left: 10px;
  color: #333;
  font-size: 14px;
}

.header__back::before {
  border: #f63 solid 3px;
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -8px;
  display: block;
  content: ' ';
  width: 12px;
  height: 12px;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.header__title {
  color: #333;
  font-size: 16px;
}
.purchaseForm__title {
  padding: 10px 29px 10px 15px;
  font-size: 14px;
  color: #999;
  position: relative;
  font-weight: 400;
}

.purchaseForm__wrapper {
  background-color: #fff;
  margin-top: 20px;
  padding-left: 15px;
}

.purchaseForm__row {
  padding: 10px 16px 10px 0;
  color: #999;
  position: relative;
  background-image: url(//www.dpfile.com/mod/mbase-css/2.0.1/dist/outline/img/repeat-x-bottom.png);
  background-position: 0 top;
  background-repeat: repeat-x;
}

.purchaseForm__row1 {
  padding: 4px 16px 0px 0;
  color: #999;
  position: relative;
  height: 36px;
}


.purchaseForm__rowLabel--1 {
  display: inline-block;
  width: 50%;
  padding-top: 7px;
}

.purchaseForm__rowLabel {
  display: inline-block;
  width: 50%;
}

.purchaseForm__rowValue {
  float: right;
}

.purchaseForm__quantity {
    width: 42px;
    border: 1px solid #bbb;
    color: #333;
    font-size: 15px;
    line-height: 28px;
    text-align: center;
    margin-left: 5px;
}

.purchaseForm__counter--dec {
    margin-left: 5px;
    box-sizing: border-box;
    text-align: center;
    display: inline-block;
    width: 30px;
    border-radius: 2px;
    padding: 5px 0 5px;
    border: 1px solid #bbb;
    color: #bbb;
    font-size: 12px;
    font-weight: 700;
}

.purchaseForm__counter--inc {
    margin-left: 5px;
    box-sizing: border-box;
    text-align: center;
    display: inline-block;
    width: 30px;
    border-radius: 2px;
    padding: 5px 0 5px;
    border: 1px solid #f63;
    color: #f63;
    font-size: 12px;
    font-weight: 700;
}

.purchaseForm__remark {
  overflow: hidden;
  height: 45px;
  line-height: 45px;
  color: #666;
  list-style: none;
  padding-left: 15px;
  margin: 0;
}

.purchaseForm__remarkItem {
  float: left;
  margin-right: 10px;
}

.purchaseForm__sign {
  background-image: url("https://www.dpfile.com/mod/app-m-tuangou/1.3.79/css/img/comm.png");
  background-size: 25px auto;
  background-position: 0 -275px;
  display: inline-block;
  height: 18px;
  width: 18px;
  vertical-align: sub;
  margin-right: 5px;
}

.purchaseForm__desc {
  font-size: 12px;
}

.purchaseForm__submit {
  display: block;
  height: 40px;
  margin: 10px 9px;
  color: #fff;
  background-color: #f63;
  border-radius: 4px;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
}

.tip {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 300;
  position: fixed;
}

.tip__alert {
  width: 280px;
  background: #fff;
  border-radius: 10px;
  left: 50%;
  top: 50%;
  border: 1px solid #efefef;
  margin: -50px 0 0 -140px;
  z-index: 10000002;
  position: fixed;
  text-align: center;
}

.tip__content {
  padding: 20px;
  border-bottom: solid 1px #efefef;
}

.tip__btns {
  font-size: 14px;
  height: 40px;
  line-height: 40px;
}

.tip__btn {
  color: #08c;
  width: 50%;
  height: 100%;
  box-sizing: border-box;
}

